"use client";

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import {
  Home,
  TrendingUp,
  TrendingDown,
  Tag,
  Building2,
  CreditCard,
  X,
} from 'lucide-react';

import { Button } from "@/components/ui/button"
import { ScrollArea } from "@/components/ui/scroll-area"
import { cn } from "@/lib/utils"

interface SidebarProps {
  isOpen: boolean;
  onToggle: () => void;
}

const Sidebar: React.FC<SidebarProps> = ({ isOpen, onToggle }) => {
  const pathname = usePathname();

  const menuItems = [
    {
      path: '/dashboard',
      icon: Home,
      label: 'Dashboard',
    },
    {
      path: '/receitas',
      icon: TrendingUp,
      label: 'Receitas',
    },
    {
      path: '/despesas',
      icon: TrendingDown,
      label: 'Despesas',
    },
    {
      path: '/categorias',
      icon: Tag,
      label: 'Categorias',
    },
    {
      path: '/bancos',
      icon: Building2,
      label: 'Bancos',
    },
    {
      path: '/cartoes',
      icon: CreditCard,
      label: 'Cartões',
    },
  ];

  return (
    <>
      {/* Overlay para mobile */}
      {isOpen && (
        <div 
          className="fixed inset-0 z-40 bg-background/80 backdrop-blur-sm lg:hidden"
          onClick={onToggle}
        />
      )}

      {/* Sidebar */}
      <div className={cn(
        "fixed inset-y-0 left-0 z-50 w-64 bg-background border-r border-border shadow-none transform transition-transform duration-300 ease-in-out lg:translate-x-0 lg:static lg:inset-0",
        isOpen ? "translate-x-0" : "-translate-x-full"
      )}>
        {/* Header */}
        <div className="flex items-center h-16 px-6 border-b border-border/50">
          <Link href="/dashboard" className="flex items-center gap-2.5 transition-opacity hover:opacity-80">
            <div className="bg-primary text-primary-foreground flex items-center justify-center w-8 h-8 rounded-lg font-bold text-sm tracking-tighter">
              SD
            </div>
            <span className="text-lg font-semibold tracking-tight">Finanças</span>
          </Link>
          <Button
            variant="ghost"
            size="icon"
            onClick={onToggle}
            className="lg:hidden ml-auto"
          >
            <X className="h-5 w-5" />
          </Button>
        </div>

        {/* Navigation */}
        <ScrollArea className="h-[calc(100vh-4rem)] py-4 px-3">
          <nav className="space-y-1">
            {menuItems.map((item) => {
              const Icon = item.icon;
              const isActive = pathname === item.path;
              
              return (
                <Link
                  key={item.path}
                  href={item.path}
                  className={cn(
                    "flex items-center gap-3 px-3 py-2 rounded-md text-sm font-medium transition-colors relative group",
                    isActive 
                      ? "bg-primary/10 text-primary" 
                      : "text-muted-foreground hover:bg-accent hover:text-accent-foreground"
                  )}
                >
                  <Icon className={cn(
                    "h-4 w-4 transition-colors",
                    isActive ? "text-primary" : "text-muted-foreground group-hover:text-accent-foreground"
                  )} />
                  {item.label}
                  {isActive && (
                    <div className="absolute left-0 w-1 h-5 bg-primary rounded-r-full" />
                  )}
                </Link>
              );
            })}
          </nav>
        </ScrollArea>
      </div>
    </>
  );
};

export default Sidebar;
